.propos_page {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.propos_section {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 223px;
    width: 1940px;
    max-width: 95%;
}



.propos_image {
    margin-top: 5%;
    width: 90%;
    height: 100%;
    border-radius: 25px;
    border-radius: 25px;
}

.propos_contenu {
    display: flex;
    flex-direction: column;
    margin-top: 5%;
    height: 800px;
    width: 60%;
}
.accordion-itemPropos{
    margin-bottom: 10px;
}

.accordion-titlePropos {
    width: 100%;
    background-color: #FF6060;
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 50px;
    font-weight: 500;
    font-size: 24px;
    border-radius: 5px;
    }

.accordion-contentPropos {
    color: #FF6060;
    background-color: #F7F7F7;
    font-size: 20px;
    line-height: 142.6%;
    margin-bottom: 10px;
    padding: 1%;
}
.margin_left{
    margin-left: 10px;
}
.margin_right{
    margin-right: 10px;
}
@media screen and (max-width: 1220px) {
    .propos_contenu {
        display: flex;
        flex-direction: column;
        width: 60%;
    }
    .accordion-titlePropos {
        width: 100%;
        background-color: #FF6060;
        color: white;
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 50px;
        font-weight: 500;
        font-size: 20px;
        border-radius: 5px;
        }

}

@media screen and (max-width: 700px) {
    .propos_image {
        display: flex;
        height: 223px;
        width: 100%;


    }

    .propos_section {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 223px;
        width: 100%;
    }

    .propos_contenu {
        display: flex;
        flex-direction: column;
        margin-top: 5%;


        width: 90%;
    }

    .accordion-contentPropos {
        color: #FF6060;
        background-color: #F7F7F7;
        font-size: 15px;
        line-height: 142.6%;
    }
    .accordion-titlePropos {
        width: 100%;
        background-color: #FF6060;
        color: white;
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 50px;
        font-weight: 500;
        font-size: 18px;
        border-radius: 5px;
        }

}